import { Component, h, Host, Prop } from '@stencil/core';

type Size = 'sm' | 'md' | 'lg';

const listItemVariants = {
    sm: 'py-4 text-sm',
    md: 'py-8 text-body',
    lg: 'py-[12px] text-body',
};

/**
 * @category Layout
 * @description A list item component for data in key:value format.
 *
 * @slot title - Placed in the title position
 * @slot - PLace custom content or items before the content
 */
@Component({
    tag: 'at-list-item',
    shadow: false,
})
export class AtListItem {
    /**
     * Optional icon shown to the left of the list item - use mat-icon name.
     */
    @Prop()
    icon?: string;

    /**
     * Optional prefix.
     */
    @Prop()
    item_prefix?: string;

    /**
     * Title of the list item.
     */
    @Prop()
    item_title: string;

    /**
     * Optional subtitle of the list item.
     */
    @Prop()
    subtitle?: string;

    /**
     * Content of the list item placed at the right of the item.
     */
    @Prop()
    content?: string;

    /**
     * Size of the list item.
     */
    @Prop()
    size?: Size = 'sm';

    /**
     * Border below the list item.
     */
    @Prop()
    border? = true;

    get sizeClasses(): string {
        return listItemVariants[this.size];
    }

    render() {
        return (
            <Host
                role="listitem"
                tabIndex={0}
                class={`flex items-center gap-3 px-4 ${this.sizeClasses} ${this.border ? 'border-border border-b border-solid' : ''}`}
            >
                <div class="flex flex-grow flex-col">
                    <div
                        class={`flex items-center justify-start gap-8 whitespace-nowrap`}
                    >
                        {this.icon && (
                            <span class="material-icons text-light items-center text-sm">
                                {this.icon}
                            </span>
                        )}

                        {this.item_prefix && (
                            <span class="text-med mr-[4px] font-normal">
                                {this.item_prefix}
                            </span>
                        )}
                        <span
                            class={`flex flex-grow truncate pr-8 font-medium ${this.item_prefix && this.subtitle ? 'flex flex-col' : ''}`}
                        >
                            <span class="flex flex-row font-normal">
                                <span class="mr-4">{this.item_title}</span>
                                <slot name="title" />
                            </span>
                            {this.subtitle && (
                                <span
                                    class="text-med inline text-sm font-normal"
                                    data-name="subtitle"
                                >
                                    {this.subtitle}
                                </span>
                            )}
                        </span>
                    </div>
                </div>

                <div class="flex flex-wrap items-end justify-end gap-8">
                    <slot />
                    {this.content && (
                        <span data-name="content">{this.content}</span>
                    )}
                </div>
            </Host>
        );
    }
}
